// input
.input-wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;

  .text {
    display: block;
  }
}

input,
input[type='text'],
select {
  border: 1px solid rgb(212, 212, 212);
  border-radius: 0.5rem;
  height: 2.5rem;
  padding: 0 0.5rem;

  &::placeholder {
    font-size: 0.875rem;
  }
}

// checkbox
.checkbox {
  cursor: pointer;
  position: relative;

  input {
    display: none;
    position: absolute;

    &[type='checkbox'] {
      &:checked {
        & ~ .label-checkbox {
          &::before {
            border: none;
            background: rgb(71, 155, 180) url('../assets/icon_checked.png') center/cover no-repeat;
          }
        }
      }
    }
  }

  .label-checkbox {
    display: flex;
    align-items: center;

    &::before {
      content: '';
      display: inline-block;
      width: 1rem;
      height: 1rem;
      background-color: #fff;
      border: 1px solid rgb(71, 155, 180);
      border-radius: 0.25rem;
      margin-right: 0.375rem;
    }
  }
}

// button
button,
.btn {
  background-color: #999;
  width: fit-content;
  height: 2rem;
  padding: 0 1rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;

  &.sm {
    height: 1.5rem;
    padding: 0 0.5rem;
    font-size: 0.75rem;
  }

  &.lg {
    height: 2.5rem;
    padding: 0 1rem;
    font-size: 1.125rem;
  }
}

.btn {
  background-color: rgb(71, 155, 180);
}

// table
table {
  width: 100%;

  tr {
    td {
      border: 1px solid #d2d2d2;
      padding: 1rem;
      text-align: center;
      font-size: 1rem;
      font-weight: 500;
    }
  }

  .checkbox {
    display: flex;
    justify-content: center;

    .label-checkbox {
      &::before {
        margin-right: 0;
      }
    }
  }
}

/*
* responsive
*/
@media (max-width: 767.98px) {
  table {
    tr {
      td {
        padding: 1rem 0.5rem;
      }
    }
  }
}
